import * as React from 'react';
import { Box } from '@chakra-ui/core';

export default function Section({ children, backgroundColor, skew = 0, ...props }) {
  return (
    <Box as="section" py={4} position="relative" {...props}>
      {skew !== 0 && (
        <Box
          position="absolute"
          top="0"
          bottom="0"
          left="0"
          right="0"
          w="100%"
          h="100%"
          backgroundColor={backgroundColor}
          zIndex="-1"
          transform={`skewY(${skew}deg)`}
          transformOrigin="top left"
        />
      )}
      {children}
    </Box>
  );
}

