function MenuItem({ children }) {
  return (
    <PseudoBox
      as="span"
      transition="all 0.2s ease"
      _hover={{ opacity: 0.8 }}
      mt={{ base: 4, md: 0 }}
      mr={6}
      display="block"
      cursor="pointer"
    >
      {children}
    </PseudoBox>
  );
}

export default function Header({ isHome }) {
  const [show, setShow] = React.useState(false);
  const handleToggle = () => setShow(!show);
  return (
    <Box
      as="header"
      position="absolute"
      top="10px"
      left="0"
      width="100%"
      zIndex={1}
      py={4}
      background={isHome ? 'transparent' : 'linear-gradient(150deg,#2f855a 15%,#1e4e8c 70%,#153e75 95%)'}
    >
      <Flex as="nav" align="center" justify="space-between" wrap="wrap" px={4} color="white">
        <h1>
          <Link href="/">
            <a>
              <Logo />
            </a>
          </Link>
        </h1>

        <Box display={{ xs: 'block', sm: 'block', md: 'none' }} onClick={handleToggle}>
          <svg fill="white" width="12px" viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg">
            <title>Menu</title>
            <path d="M0 3h20v2H0V3zm0 6h20v2H0V9zm0 6h20v2H0v-2z" />
          </svg>
        </Box>

        <Box
          display={{ xs: show ? 'block' : 'none', sm: show ? 'block' : 'none', md: 'flex' }}
          width={{ sm: 'full', md: 'auto' }}
          justifyContent="flex-end"
          alignItems="center"
          flexGrow={1}
        >
          <MenuItem>
            <Link href="/chi-siamo">
              <a>Chi siamo</a>
            </Link>
          </MenuItem>
          <MenuItem>Soluzioni IT</MenuItem>
          <MenuItem>Software</MenuItem>
          <MenuItem>Noleggio</MenuItem>
          <MenuItem>Assistenza</MenuItem>
        </Box>
      </Flex>
    </Box>
  );
}
