import * as React from 'react';
import { useSprings, useSpring, animated, interpolate } from 'react-spring';
import { useDrag } from 'react-use-gesture';
import { Box, Flex, Button, Icon } from '@chakra-ui/core';

export default function Carousel({ activeIndex = 0, auto = 0, children = [], ...props }) {
  const sliderRef = React.useRef();
  const [slide, setSlide] = React.useState(0);
  const [isDragging, setDragging] = React.useState(false);

  // Initialize slides with spring
  const [springProps, setSpringProps] = useSprings(children.length, (index) => ({
    offset: index,
  }));

  const bind = useDrag(
    ({ down, movement: [xDelta], direction: [xDir], distance, cancel, first }) => {
      if (first) {
        setDragging(true);
      }
      if (sliderRef && sliderRef.current && sliderRef.current.parentElement) {
        const { width } = sliderRef.current.parentElement.getBoundingClientRect();

        if (down && distance > width / 3) {
          if (cancel) cancel();
          setSlide((slide + (xDir > 0 ? -1 : 1)) % children.length);
        }
        setSpringProps((index) => ({
          offset: (down ? xDelta : 0) / width + (index - slide),
        }));
      }
    },
    { filterTaps: true, delay: true, axis: 'x', enabled: true /*children.length > 1*/ }
  );

  // Triggered on slide change
  React.useEffect(() => {
    setSpringProps((index) => ({ offset: index - slide }));
  }, [slide, setSpringProps]);

  const intervalRef = React.useRef();

  function enableAutoplay() {
    if (auto > 0) {
      intervalRef.current = setInterval(() => {
        setSlide((old) => (old + 1) % children.length);
      }, auto);
    }
  }

  function disableAutoplay() {
    clearInterval(intervalRef.current);
  }

  // Effect for autosliding
  React.useEffect(() => {
    if (auto > 0) {
      intervalRef.current = setInterval(() => {
        setSlide((old) => (old + 1) % children.length);
      }, auto);
    }

    return () => {
      if (intervalRef.current) clearInterval(intervalRef.current);
    };
  }, []);

  const nextSlide = () => {
    setSlide((slide + 1) % children.length);
  };

  const previousSlide = () => {
    setSlide((slide - 1) % children.length);
  };

  const prevSlide = usePrevious(slide);

  const { x, sc } = useSpring({
    x: slide * 2 + 0.5,
    sc: slide,
  });

  const scale = sc
    .interpolate({
      range: [Math.min(prevSlide, slide), Math.max(prevSlide, slide)],
      output: [0, 1],
      extrapolate: 'clamp',
    })
    .interpolate({ range: [0, 0.5, 1], output: [1, 1.5, 1] });

  return (
    <Box minH="320px" w="100%" ref={sliderRef} {...props} onMouseOver={disableAutoplay} onMouseOut={enableAutoplay}>
      <Box position="relative" overflow="hidden" minH="320px" w="100%">
        {children.length > 1 ? (
          <>
            <Button
              position="absolute"
              left="0"
              top="50%"
              transform="translateY(-50%)"
              zIndex="2"
              backgroundColor="rgba(255, 255,255, 0.2)"
              _hover={{
                backgroundColor: 'rgba(255, 255,255, 0.3)',
              }}
              _active={{
                backgroundColor: 'rgba(255, 255,255, 0.4)',
              }}
              onClick={previousSlide}
            >
              <Icon name="chevron-left" />
            </Button>
            <Button
              position="absolute"
              right="0"
              top="50%"
              transform="translateY(-50%)"
              zIndex="2"
              backgroundColor="rgba(255, 255,255, 0.2)"
              onClick={nextSlide}
            >
              <Icon name="chevron-right" />
            </Button>
          </>
        ) : null}
        {springProps.map(({ offset }, index) => (
          <animated.div
            {...bind()}
            key={index}
            style={{
              transform: offset.interpolate((offsetX) => `translate3d(${offsetX * 100}%, 0, 0)`),
              position: 'absolute',
              display: 'inline-block',
              width: '100%',
              height: '100%',
              willChange: 'transform',
            }}
          >
            <Box minH="320px" w="100%" userSelect={isDragging ? 'none' : 'auto'}>
              {children[index]}
            </Box>
          </animated.div>
        ))}
      </Box>
      {children.length > 1 ? (
        <Flex mx={[2, 8, 24]} alignItems="center" position="relative">
          {children.map((_, i) => (
            <Flex key={i} mx="0.5rem" h="1rem" w="1rem" align="center" justify="center">
              <Box
                w="60%"
                h="60%"
                borderRadius="50%"
                backgroundColor="gray.200"
                cursor="pointer"
                onClick={() => setSlide(i)}
              />
            </Flex>
          ))}
          <AnimatedBox
            position="absolute"
            h="1rem"
            w="1rem"
            borderRadius="50%"
            backgroundColor="green.200"
            cursor="pointer"
            style={{
              transform: interpolate([x, scale], (x, scale) => `translateX(${x}rem) scale(${scale})`),
            }}
          />
        </Flex>
      ) : null}
    </Box>
  );
}

const AnimatedBox = animated(Box);

function usePrevious(value) {
  // The ref object is a generic container whose current property is mutable ...
  // ... and can hold any value, similar to an instance property on a class
  const ref = React.useRef(0);

  // Store current value in ref
  React.useEffect(() => {
    ref.current = value;
  }, [value]); // Only re-run if value changes

  // Return previous value (happens before update in useEffect above)
  return ref.current;
}
